import React, { Component } from 'react'
import Project from './Project'

class Projects extends Component {

    render() {

        let cbn = require("./../img/color_by_number_2.jpg");
        let pfp = require("./../img/food_picker.jpg");
        let pbb = require("./../img/pokemon_boba_1.PNG");
        let sn = require("./../img/sticky_notes.png");
        let wu = require("./../img/website_updater.png");
        let rc = require("./../img/robot_car.jpg");

        return (
            <main id="about-main" className="projects">

                <div className="d-inline">

                    <div className="content-panel content-panel-bg">

                        <div>
                            <h2 className="heading-title">Projects</h2>
                            <p>(hover or tap for more info)</p>
                        </div>
                        
                        <Project logo={cbn} companyName="Color by Photo" date="Mar 2020 - Mar 2020"/>
                        <Project logo={pfp} companyName="Project Food Picker" date="Dec 2019 - Jan 2020"/>
                        <Project logo={pbb} companyName="Pokemon Boba Black" date="Nov 2019 - Dec 2019"/>
                        <Project logo={sn} companyName="Sticky Notes" date="June 2019 - June 2019"/>
                        <Project logo={wu} companyName="Website Updater" date="June 2019 - June 2019"/>
                        <Project logo={rc} companyName="Robot Car" date="May 2019 - May 2019"/>

                    </div>
                </div>
            </main>
        )
    }
}

export default Projects